.app-wrapper {
  display: grid;
  grid-template-areas:
    "h"
    "c"
    "f";
  grid-template-rows: 50px 1fr 120px;
  max-width: 2560px;
  margin: 0 auto;
}

@media (max-width: 1000px) {
  .app-wrapper {
    grid-template-rows: 50px 1fr 95px;
  }
}

@media (max-width: 420px) {
  .app-wrapper {
    grid-template-rows: 50px 1fr 90px;
  }
}
